import React, { Component } from "react";
import Item from "./child/Item";
import "./style.css";
import Pagination from "../../components/Pagination";

export default function HomePage() {
  return (
    <div className="container-fluid">
      <div style={{ display: "flex", justifyContent: "space-between" }}>
        <h4 className="h4 title_content mb-0 text-gray-800">
          Tổng hợp kết quả
        </h4>{" "}
      </div>

      <div className="card shadow mb-4">
        <div
          class="card-header py-3"
          style={{ display: "flex", justifyContent: "space-between" }}
        >
          <div style={{display : "flex"}}>
            <h6 class="title-card">Tổng hợp theo phòng ban</h6>

            <form>
              <div className="input-group mb-6" style={{ padding: "0px 20px" }}>
                <input
                  type="search"
                  name="txtSearch"
                  className="form-control"
                  placeholder="Tìm phòng ban"
                  
                  style={{ maxWidth: "400px" }}
                />
                <div className="input-group-append">
                  <button className="btn btn-primary" type="submit">
                    <i className="fa fa-search" />
                  </button>
                </div>
              </div>
            </form>
          </div>
          <button
            style={{ marginRight: "10px" }}
            class={`btn btn-danger btn-icon-split btn-sm  `}
          >
            <span class="icon text-white-50">
              <i class="fas fa-file-export"></i>
            </span>
            <span style={{ color: "white" }} class="text">
              Export Excel
            </span>
          </button>
        </div>
        <div className="card-body">
          <div className="table-responsive report-staff not-border-head-table">
            <div
              id="dataTable_wrapper"
              className="dataTables_wrapper no-footer"
            >
              <table
                className="table no-footer dataTable table-striped report-staff"
                id="dataTable"
                width="100%"
                cellSpacing={0}
                aria-describedby="dataTable_info"
                style={{ width: "100%" }}
              >
                <thead>
                  <tr style={{ background: "white" }}>
                    <th
                      className="sorting_disabled sorting_asc"
                      rowSpan={1}
                      colSpan={1}
                      aria-label="STT"
                    >
                      STT
                    </th>
                    <th
                      className="sorting_disabled"
                      rowSpan={1}
                      colSpan={1}
                      aria-label="Họ và tên"
                    >
                      Phòng ban
                    </th>
                    <th
                      className="sorting_disabled"
                      rowSpan={1}
                      colSpan={1}
                      aria-label="Tên tài khoản"
                    >
                      Tổng số bài Test phải làm
                    </th>
                    <th
                      className="sorting_disabled"
                      rowSpan={1}
                      colSpan={1}
                      aria-label="Số điện thoại"
                    >
                      Tình trạng hoàn thành
                    </th>

                    <th
                      className="sorting_disabled"
                      rowSpan={1}
                      colSpan={1}
                      aria-label="Số điện thoại"
                    >
                      Tỉ lệ đạt
                    </th>
                    <th
                      className="sorting_disabled"
                      rowSpan={1}
                      colSpan={1}
                      aria-label="Số điện thoại"
                    >
                      Tỉ lệ giỏi
                    </th>
                    <th
                      className="sorting_disabled"
                      rowSpan={1}
                      colSpan={1}
                      aria-label="Số điện thoại"
                    >
                      Tỉ lệ xuất sắc
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <Item></Item>
                  <Item></Item>
                  <Item></Item>
                </tbody>
              </table>
            </div>
          </div>
          <br />
          <Pagination></Pagination>
        </div>
      </div>

      <div className="card shadow mb-4">
        <div
          class="card-header py-3"
          style={{ display: "flex", justifyContent: "space-between" }}
        >
 <div style={{display : "flex"}}>
            <h6 class="title-card">Tổng hợp theo nhân viên</h6>

            <form>
              <div className="input-group mb-6" style={{ padding: "0px 20px" }}>
                <input
                  type="search"
                  name="txtSearch"
                  className="form-control"
                  placeholder="Tìm nhân viên"
                  
                  style={{ maxWidth: "400px" }}
                />
                <div className="input-group-append">
                  <button className="btn btn-primary" type="submit">
                    <i className="fa fa-search" />
                  </button>
                </div>
              </div>
            </form>
          </div>          <button
            style={{ marginRight: "10px" }}
            class={`btn btn-danger btn-icon-split btn-sm  `}
          >
            <span class="icon text-white-50">
              <i class="fas fa-file-export"></i>
            </span>
            <span style={{ color: "white" }} class="text">
              Export Excel
            </span>
          </button>
        </div>
        <div className="card-body">
          <div className="table-responsive report-staff not-border-head-table">
            <div
              id="dataTable_wrapper"
              className="dataTables_wrapper no-footer"
            >
              <table
                className="table no-footer dataTable table-striped report-staff"
                id="dataTable"
                width="100%"
                cellSpacing={0}
                aria-describedby="dataTable_info"
                style={{ width: "100%" }}
              >
                <thead>
                  <tr style={{ background: "white" }}>
                    <th
                      className="sorting_disabled sorting_asc"
                      rowSpan={1}
                      colSpan={1}
                      aria-label="STT"
                    >
                      STT
                    </th>
                    <th
                      className="sorting_disabled"
                      rowSpan={1}
                      colSpan={1}
                      aria-label="Họ và tên"
                    >
                      Tên nhân viên
                    </th>
                    <th
                      className="sorting_disabled"
                      rowSpan={1}
                      colSpan={1}
                      aria-label="Tên tài khoản"
                    >
                      Tổng số bài Test phải làm
                    </th>
                    <th
                      className="sorting_disabled"
                      rowSpan={1}
                      colSpan={1}
                      aria-label="Số điện thoại"
                    >
                      Tình trạng hoàn thành
                    </th>

                    <th
                      className="sorting_disabled"
                      rowSpan={1}
                      colSpan={1}
                      aria-label="Số điện thoại"
                    >
                      Tỉ lệ đạt
                    </th>
                    <th
                      className="sorting_disabled"
                      rowSpan={1}
                      colSpan={1}
                      aria-label="Số điện thoại"
                    >
                      Tỉ lệ giỏi
                    </th>
                    <th
                      className="sorting_disabled"
                      rowSpan={1}
                      colSpan={1}
                      aria-label="Số điện thoại"
                    >
                      Tỉ lệ xuất sắc
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <Item></Item>
                  <Item></Item>
                  <Item></Item>
                </tbody>
              </table>
            </div>
          </div>
          <br />
          <Pagination></Pagination>
        </div>
      </div>
    </div>
  );
}
